<template></template>

<script>
    import Element from '../../modeling/Element'
    import VueSuperMethod from 'vue-super-call'

    Vue.prototype.$super = VueSuperMethod

    export default {
        mixins: [Element],
        name: 'event-storming-element',
        props: {},
        created: function () {
        },
        watch: {
        },
        mounted: function () {
        },
        methods: {
            
            //@Override
            onRemoveShape() {
                var me = this

                //super.onRemoveShape();

                me.$super(Element).onRemoveShape() //  ----- >  

                var designer = me.getComponent('modeling-designer')

                var obj = {
                    state: 'delete',
                    element: me.value
                }

                if (designer.parmType == 'local' || designer.specVersion == '1.0') {

                    if (me.value._type == 'org.uengine.modeling.model.Relation') {
                        me.$EventBus.$emit(`${me.value.relationView.id}`, obj)
                    } else {
                        me.$EventBus.$emit(`${me.value.elementView.id}`, obj)
                    }

                } else if (designer.specVersion == '2.0' && !designer.isRead) {

                    if (me.value._type == 'org.uengine.modeling.model.Relation') {
                        firebase.database()
                            .ref('definitions/' + designer.parmProjectId + '/queue')
                            .push({
                                state: 'relationDelete',
                                editUid: localStorage.getItem('uid'),
                                timeStamp: new Date().getTime(),
                                item: JSON.stringify(obj.element)
                            })
                    } else {
                        firebase.database()
                            .ref('definitions/' + designer.parmProjectId + '/queue')
                            .push({
                                state: 'elementDelete',
                                editUid: localStorage.getItem('uid'),
                                timeStamp: new Date().getTime(),
                                item: JSON.stringify(obj.element)
                            })

                    }
                }

            }

        
        }
    }
</script>


